export type PersonType = {
    _id: number
    name: string
    age: number
}

const initState: PersonType[] = [
    {_id: 0, name: '', age: 3},
    {_id: 1, name: '', age: 66},
    {_id: 2, name: '', age: 16},
    {_id: 3, name: '', age: 44},
    {_id: 4, name: '', age: 40},
    {_id: 5, name: '', age: 55},
]

type SortType = {
    type: 'sort'
    payload: 'up' | 'down'
}

type CheckType = {
    type: 'check'
    payload: number
}

type ActionType = SortType | CheckType

export const homeWorkReducer = (state: PersonType[] = initState, action: ActionType): PersonType[] => {
    switch (action.type) {
        case 'sort': {
            // need to fix
            return action.payload === 'up'
                ? [...state.sort((a, b) => a.name === b.name ? 0 : +(a.name > b.name) || -1)]
                : [...state.sort((a, b) => a.name === b.name ? 0 : +(a.name < b.name) || -1)]
        }
        case 'check': {
            // need to fix
            return state.filter(s => s.age > action.payload)
        }
        default:
            return state
    }
}